import HtmlElement from '../HtmlElement';
import ImgHtmlElement from '../ImgHtmlElement';

// import ellipse from '../../../assets/images/ellipse.svg';

export default class MainPage extends HtmlElement {
    baseClass = 'title-block';
    children = {};

    constructor({
        parentEl,
        numPath,
        titleText
    }) {
        super({
            parentEl
        });

        this.numPath = numPath;
        this.titleText = titleText;

        this.addClasses();
        this.appendEls();
    }

    appendEls() {
        this.children.num = new ImgHtmlElement({ parentEl: this.el, src: this.numPath, alt: 'номер 1' });
        this.children.num.addClasses([`${this.baseClass}__num`]);
        this.children.num.appendToParent();

        // eslint-disable-next-line
        this.children.ellipse = new HtmlElement({ parentEl: this.el, innerHTML: '<svg width="600" height="207" preserveAspectRatio="none" viewBox="0 0 600 207" fill="none" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><path d="M277.5 17.9997C477.372 -5.7546 582 39.7816 582 70.8664C582 101.951 536.081 150.201 316.36 187.318C147.496 215.843 26.5262 166.439 23.5635 148.809C19.977 127.468 33.5 46.9985 277.5 17.9997Z" stroke="url(#pattern0_2074_455)" stroke-width="20"></path><defs><pattern id="pattern0_2074_455" patternContentUnits="objectBoundingBox" width="1" height="1"><use xlink:href="#image0_2074_455" transform="matrix(0.00173114 0 0 0.00519676 -0.00739806 0.00332198)"></use></pattern><image id="image0_2074_455" width="582" height="197" xlink:href="data:image/png;base64,"></image></defs></svg>'})
        this.children.ellipse.addClasses([`${this.baseClass}__ellipse`]);
        this.children.ellipse.appendToParent();

        // eslint-disable-next-line
        // <svg width="575" height="195" viewBox="0 0 575 195" fill="none" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
        // eslint-disable-next-line
        // const svg = new HtmlElement({ parentEl: this.children.ellipse.el, tag: 'svg', innerHTML: '' });

        // svg.el.setAttribute('width', '575');
        // svg.el.setAttribute('height', '195');
        // svg.el.setAttribute('viewBox', '0 0 575 195');
        // svg.el.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
        // svg.el.setAttribute('xmlns:xlink', 'http://www.w3.org/1999/xlink');

        // svg.appendToParent();

        this.children.title = new HtmlElement({ parentEl: this.el, tag: 'h2', innerHTML: this.titleText });
        this.children.title.addClasses([`${this.baseClass}__title`]);
        this.children.title.appendToParent();
    }
}
